/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"jumbotron jumbotron-fluid\">\r\n  <div class=\"container\">\r\n    <h1>Privacy Policy</h1>\r\n  </div>\r\n</div>\r\n\r\n<div class=\"container\">\r\n\r\n  <p>Effective date: August 29, 2018</p>\r\n\r\n\r\n  <p>CADEOS.io (\"us\", \"we\", or \"our\") operates the https://cadeos.io website (the\r\n    \"Service\").</p>\r\n\r\n  <p>This page informs you of our policies regarding the collection, use, and disclosure of personal data when you use\r\n    our Service and the choices you have associated with that data. This Privacy Policy for CADEOS.io is\r\n    powered by <a href=\"https://www.freeprivacypolicy.com/free-privacy-policy-generator.php\">FreePrivacyPolicy.com</a>.\r\n  </p>\r\n\r\n  <p>We use your data to provide and improve the Service. By using the Service, you agree to the collection and use of\r\n    information in accordance with this policy. Unless otherwise defined in this Privacy Policy, terms used in this\r\n    Privacy Policy have the same meanings as in our Terms and Conditions, accessible from\r\n    https://cadeos.io</p>\r\n\r\n\r\n  <h2>Information Collection And Use</h2>\r\n\r\n  <p>We collect several different types of information for various purposes to provide and improve our Service to\r\n    you.</p>\r\n\r\n  <h3>Types of Data Collected</h3>\r\n\r\n  <h4>Personal Data</h4>\r\n\r\n  <p>While using our Service, we may ask you to provide us with certain personally identifiable information that can be\r\n    used to contact or identify you (\"Personal Data\"). Personally identifiable information may include, but is not\r\n    limited to:</p>\r\n\r\n  <ul>\r\n    <li>Email address</li>\r\n    <li>First name and last name</li>\r\n    <li>Cookies and Usage Data</li>\r\n  </ul>\r\n\r\n  <h4>Usage Data</h4>\r\n\r\n  <p>We may also collect information how the Service is accessed and used (\"Usage Data\"). This Usage Data may include\r\n    information such as your computer's Internet Protocol address (e.g. IP address), browser type, browser version, the\r\n    pages of our Service that you visit, the time and date of your visit, the time spent on those pages, unique device\r\n    identifiers and other diagnostic data.</p>\r\n\r\n  <h4>Tracking & Cookies Data</h4>\r\n  <p>We use cookies and similar tracking technologies to track the activity on our Service and hold certain\r\n    information.</p>\r\n  <p>Cookies are files with small amount of data which may include an anonymous unique identifier. Cookies are sent to\r\n    your browser from a website and stored on your device. Tracking technologies also used are beacons, tags, and\r\n    scripts to collect and track information and to improve and analyze our Service.</p>\r\n  <p>You can instruct your browser to refuse all cookies or to indicate when a cookie is being sent. However, if you do\r\n    not accept cookies, you may not be able to use some portions of our Service.</p>\r\n  <p>Examples of Cookies we use:</p>\r\n  <ul>\r\n    <li><strong>Session Cookies.</strong> We use Session Cookies to operate our Service.</li>\r\n    <li><strong>Preference Cookies.</strong> We use Preference Cookies to remember your preferences and various\r\n      settings.\r\n    </li>\r\n    <li><strong>Security Cookies.</strong> We use Security Cookies for security purposes.</li>\r\n  </ul>\r\n\r\n  <h2>Use of Data</h2>\r\n\r\n  <p>CADEOS.io uses the collected data for various purposes:</p>\r\n  <ul>\r\n    <li>To provide and maintain the Service</li>\r\n    <li>To notify you about changes to our Service</li>\r\n    <li>To allow you to participate in interactive features of our Service when you choose to do so</li>\r\n    <li>To provide customer care and support</li>\r\n    <li>To provide analysis or valuable information so that we can improve the Service</li>\r\n    <li>To monitor the usage of the Service</li>\r\n    <li>To detect, prevent and address technical issues</li>\r\n  </ul>\r\n\r\n  <h2>Transfer Of Data</h2>\r\n  <p>Your information, including Personal Data, may be transferred to — and maintained on — computers located outside of\r\n    your state, province, country or other governmental jurisdiction where the data protection laws may differ than\r\n    those from your jurisdiction.</p>\r\n  <p>If you are located outside Germany and choose to provide information to us, please note that we transfer the data,\r\n    including Personal Data, to Germany and process it there.</p>\r\n  <p>Your consent to this Privacy Policy followed by your submission of such information represents your agreement to\r\n    that transfer.</p>\r\n  <p>CADEOS.io will take all steps reasonably necessary to ensure that your data is treated securely and in\r\n    accordance with this Privacy Policy and no transfer of your Personal Data will take place to an organization or a\r\n    country unless there are adequate controls in place including the security of your data and other personal\r\n    information.</p>\r\n\r\n  <h2>Disclosure Of Data</h2>\r\n\r\n  <h3>Legal Requirements</h3>\r\n  <p>CADEOS.io may disclose your Personal Data in the good faith belief that such action is necessary to:</p>\r\n  <ul>\r\n    <li>To comply with a legal obligation</li>\r\n    <li>To protect and defend the rights or property of CADEOS.io</li>\r\n    <li>To prevent or investigate possible wrongdoing in connection with the Service</li>\r\n    <li>To protect the personal safety of users of the Service or the public</li>\r\n    <li>To protect against legal liability</li>\r\n  </ul>\r\n\r\n  <h2>Security Of Data</h2>\r\n  <p>The security of your data is important to us, but remember that no method of transmission over the Internet, or\r\n    method of electronic storage is 100% secure. While we strive to use commercially acceptable means to protect your\r\n    Personal Data, we cannot guarantee its absolute security.</p>\r\n\r\n  <h2>Service Providers</h2>\r\n  <p>We may employ third party companies and individuals to facilitate our Service (\"Service Providers\"), to provide the\r\n    Service on our behalf, to perform Service-related services or to assist us in analyzing how our Service is used.</p>\r\n  <p>These third parties have access to your Personal Data only to perform these tasks on our behalf and are obligated\r\n    not to disclose or use it for any other purpose.</p>\r\n\r\n  <h3>Analytics</h3>\r\n  <p>We may use third-party Service Providers to monitor and analyze the use of our Service.</p>\r\n  <ul>\r\n    <li>\r\n      <p><strong>Google Analytics</strong></p>\r\n      <p>Google Analytics is a web analytics service offered by Google that tracks and reports website traffic. Google\r\n        uses the data collected to track and monitor the use of our Service. This data is shared with other Google\r\n        services. Google may use the collected data to contextualize and personalize the ads of its own advertising\r\n        network.</p>\r\n      <p>You can opt-out of having made your activity on the Service available to Google Analytics by installing the\r\n        Google Analytics opt-out browser add-on. The add-on prevents the Google Analytics JavaScript (ga.js,\r\n        analytics.js, and dc.js) from sharing information with Google Analytics about visits activity.</p>\r\n      <p>For more information on the privacy practices of Google, please visit the Google Privacy & Terms web page: <a\r\n        href=\"https://policies.google.com/privacy?hl=en\">https://policies.google.com/privacy?hl=en</a></p>\r\n    </li>\r\n  </ul>\r\n\r\n\r\n  <h2>Links To Other Sites</h2>\r\n  <p>Our Service may contain links to other sites that are not operated by us. If you click on a third party link, you\r\n    will be directed to that third party's site. We strongly advise you to review the Privacy Policy of every site you\r\n    visit.</p>\r\n  <p>We have no control over and assume no responsibility for the content, privacy policies or practices of any third\r\n    party sites or services.</p>\r\n\r\n\r\n  <h2>Children's Privacy</h2>\r\n  <p>Our Service does not address anyone under the age of 18 (\"Children\").</p>\r\n  <p>We do not knowingly collect personally identifiable information from anyone under the age of 18. If you are a\r\n    parent or guardian and you are aware that your Children has provided us with Personal Data, please contact us. If we\r\n    become aware that we have collected Personal Data from children without verification of parental consent, we take\r\n    steps to remove that information from our servers.</p>\r\n\r\n\r\n  <h2>Changes To This Privacy Policy</h2>\r\n  <p>We may update our Privacy Policy from time to time. We will notify you of any changes by posting the new Privacy\r\n    Policy on this page.</p>\r\n  <p>We will let you know via email and/or a prominent notice on our Service, prior to the change becoming effective and\r\n    update the \"effective date\" at the top of this Privacy Policy.</p>\r\n  <p>You are advised to review this Privacy Policy periodically for any changes. Changes to this Privacy Policy are\r\n    effective when they are posted on this page.</p>\r\n\r\n\r\n  <h2>Contact Us</h2>\r\n  <p>If you have any questions about this Privacy Policy, please contact us:</p>\r\n  <ul>\r\n    <li>By email: g.galindo@cadeos.io</li>\r\n\r\n  </ul>\r\n\r\n</div>\r\n");

